import React from 'react';
import styled from 'styled-components';
import TabItem from './TabItem';

const StyledTabList = styled("ul")`
  display: flex;
  /* flex-direction: column; */
  justify-content: space-between;
  margin: 20px 0 50px;
  color: #fff;
  font-size: 14px;

  li {
    position: relative;
    padding: 20px 0;
    cursor: pointer;
    color: #999;
  }

  li.active {
    color: #fff;
  }

  li::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 50%;
      display: block;
      width: 0;
      height: 2px;
      background-color: #fff;
      transform: translateX(-50%);
      transition: width 0.3s;
  }

  li.active::after {
    width:100%;
  }
`

const TabList = () => {
  const [activeIndex, setActiveIndex] = React.useState(0);

  function handleTabClick(index) {
    setActiveIndex(index)
  }

  const tabArr = [
    {
      tabTitle: (
        <li key="tab1" className={activeIndex === 0 ? "active" : ""} onClick={() => handleTabClick(0)}>100% </li>
      ),
      tabContent: (
        <TabItem
          src={"https://via.placeholder.com/400x800/eee/ddd?text=img1"}
          alt={"img1"}
          title={"100%  "}
          subTitle={"HP    100%      ."}
        />
      )
    },
    {
      tabTitle: (
        <li key="tab2" className={activeIndex === 1 ? "active" : ""} onClick={() => handleTabClick(1)}></li>
      ),
      tabContent: (
        <TabItem
          src={"https://via.placeholder.com/400x800/eee/ddd?text=img2"}
          alt={"img2"}
          title={", "}
          subTitle={"  2  ,  ."}
        />
      )
    },
    {
      tabTitle: (
        <li key="tab3" className={activeIndex === 2 ? "active" : ""} onClick={() => handleTabClick(2)}></li>
      ),
      tabContent: (
        <TabItem
          src={"https://via.placeholder.com/400x800/eee/ddd?text=img3"}
          alt={"img3"}
          title={""}
          subTitle={"  3%,  25      ."}
        />
      )
    },
    {
      tabTitle: (
        <li key="tab4" className={activeIndex === 3 ? "active" : ""} onClick={() => handleTabClick(3)}></li>
      ),
      tabContent: (
        <TabItem
          src={"https://via.placeholder.com/400x800/eee/ddd?text=img1"}
          alt={"img1"}
          title={" "}
          subTitle={" PC,   .     ."}
        />
      )
    },
  ]

  return (
    <>
      <StyledTabList>
        {tabArr.map((item, idx) => {
          return item.tabTitle
        })}
      </StyledTabList>
      <div>
        {tabArr[activeIndex].tabContent}
      </div>
    </>
  );
};

export default TabList;